#body{
	background-color: #43008e;
	color:yellow;
    width:100%;
}
body{
	background-color: #43008e;
}
#slides{
    width: 100%;
    height:30em;
}

.organize{
	display:flex;
	flex-wrap: wrap;
	gap: 40px;
	position:relative;
	top:230px;
}


a{
	text-decoration :none;
	font-family: Cursive;
	color:yellow;
	font-size: 2em;
	
}
#menu{
	gap: 14%;
    width: 100%;
	display: flex;
	position: fixed;
	z-index: 2;
	background-color: #7c06ff;

	
}
a:hover{
	
	background-color: #a95bff;
}
#header{
    

    height: 170px;
	background-color:#6400d3;
	position:fixed;
	width: 100%;
	top:0%;	
	z-index: 2;
	
}


.text{
	position:absolute;
	top: 50px;
}

.list{
	position : relative;
	bottom: 30px;
	left: 50px;
}
#footer{
	display:flex;
	gap: 50px;
	position:relative;
	top:250px;
	background-color: grey;
    width: 100%;;
}

#ShoppingContainer{
	display:flex;
	gap:50px;
}
.shopping{
	position: relative;
	left: 50px;
	border-radius: 5%;
	width: 250px;
	height: 210px;
	background-color: #ee0707;
	
}
.ShoppingInfo {
	height: 69px; 
	width: 250px;
	position: absolute;
	border-radius: 5%;
	bottom: 0px;
	display: flex;
	gap: 40px;

}
.ShoppingImage {

	position: absoloute;
	width: 250px;
	height: 140px;
	
}
.buy{
	
	position:absolute;
	bottom: 5%;
	right: 3%;
	display: none;
	background-color:#ffb804;
	width: 70px;
	height: 40px;
	border-radius: 25%;
	animation-name: buys;
	animation-duration: 2s;
	
}

.shopping:hover .buy {
	display:block;

}
.shopping:hover img{
	opacity: 50%;
}

@keyframes buys{
	from {opacity: 0%;}
	to {opacity: 100%;}
	
}
